.login {
  min-height: 90vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  &__img {
    width: 400px;
    animation: slide-down 0.5s ease;
  }

  &__form-box {
    height: 30rem;
    width: 25rem;
    animation: slide-up 0.5s ease;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    border-radius: 20px;
    padding: 10px;
  }
  &__title {
    text-align: center;
    margin: 10px 0px 10px 0px;
    font-family: "Tilt Prism", cursive;
    font-size: 50px;
    color: #405cf5;
  }
  &__form {
    font-size: 1.6rem;
    outline: none;
  }
  &__input {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 1rem auto;
  }
  &__placeholder {
    padding: 1rem;
    width: 20rem;
    border: 1px solid #c0c0c0;
    border-radius: 10px;
  }
  &__button-box {
    display: flex;
    justify-content: center;
  }
  &__button {
    background-color: #405cf5;
    border-radius: 6px;
    border-width: 0;
    box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
      rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    font-size: 70%;
    height: 44px;
    line-height: 1.15;
    margin: 12px 0 0;
    padding: 0 25px;
    position: relative;
    text-align: center;
    width: 22rem;
    &--google {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #ffba00;
      border-radius: 6px;
      border-width: 0;
      box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
        rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
      box-sizing: border-box;
      color: #fff;
      cursor: pointer;
      font-size: 70%;
      height: 44px;
      line-height: 1.15;
      margin: 12px 0 0;
      padding: 0 25px;
      position: relative;
      width: 22rem;
    }
  }

  &__connect-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__break-text {
    color: #c0c0c0;
    font-family: "Josefin Sans", sans-serif;
    text-align: center;
    margin: 5px 0px 10px 0px;
    font-size: 20px;
  }
  &__logo-g {
    width: 20px;
    margin-right: 10px;
  }

  &__text {
    text-align: center;
    font-family: "Josefin Sans", sans-serif; 
    background-color: #c0c0c0;
    padding: 7px;
    border-radius: 5px;
    font-size: 15px;
  }

  &__reset{
    font-family: "Josefin Sans", sans-serif; 
    background-color: #c0c0c0;
    padding: 7px;
    border-radius: 5px;
    font-size: 15px;
    margin: 10px;
    text-decoration: none;
    &:visited {
      color: black;
    }

    &:hover {
      color: red;
    }
  }

  &__text-register {
    font-family: "Josefin Sans", sans-serif;
    text-decoration: none;
    font-size: 20px;
    margin-left: 10px;
    color: #1A73E8;

    &:visited {
      color: #1A73E8;
    }

    &:hover {
      color: red;
    }
  }
  &__register {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
  }
  @keyframes slide-up {
    0% {
      transform: translateY(-5rem);
    }
    100% {
      transform: translateY(0);
    }
  }
  @keyframes slide-down {
    0% {
      transform: translateY(5rem);
    }
    100% {
      transform: translateY(0);
    }
  }
}

@media screen and (max-width: 700px) {
}
